.App {
  padding: 30px;
  display: flex;
  max-width: 1920px;
  max-height: max-content;
  flex-wrap: wrap;
}

.date-component {
  margin-left: 900px;
  position: absolute;
  margin-bottom: 100px;
}

.boss {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-block: 10px;
  margin-inline: 10px;
  width: fit-content;
  height: fit-content;
  border-radius: 10px;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.all-tasklists {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.App > div {
  margin-right: 50px;
}

.error {
  border: red 1px solid;
  display: block;
  display: inline;
  justify-content: right;
}

.error-message {
  display: inline;
  color: red;
  font-size: 14px;
}

.active-filter {
  font-size: large;
  background-color: rgb(48, 119, 200);
  border-radius: 5px;
}

.is-done {
  opacity: 0.3;
}

.editable-span {
  display: inline-block;

  margin-left: 5px;
  flex-direction: row;
  font-family: cursive;
}
.main-input {
  justify-content: center;

  display: inline-block;
  flex-direction: row;
}

.due {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 80px;
  height: min-content;
}

.task-list {
  display: flex;
  flex-direction: column;
  margin-right: 500px;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap(default);
  margin-left: 500px;
}

.standart-input {
  display: flex;
  flex-direction: row;

  justify-content: center;
  border-radius: 5px;
  margin: 5px;
  transition-duration: 0.2s;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.input-button {
  transition: width 0.5s, height 0.5;
}
.input-content {
  display: flex;

  flex-wrap: nowrap;

  justify-content: center;
}

.task-list h1 {
  justify-content: center;
  position: relative;
}

.tasks-buttons {
  font-size: large;
  cursor: pointer;
  border-radius: 5px;
  transition-duration: 0.2s;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.buttons {
  display: flex;
  justify-content: space-evenly;
  margin-inline: 15px;
  margin-block: 10px;
}
.tasks-buttons:hover {
  background-color: rgb(66, 48, 200);
}

.tasks-buttons:active {
  font-size: large;
  background-color: rgb(65, 102, 212);
  box-shadow: 0 3px #666;
  transform: translateY(2px);
}
.remove-buttons {
  cursor: pointer;
  color: #f4472d;

  margin-left: 2px;
  height: 21px;
  font-size: 11px;
  padding-top: 1.13px;
  transition-duration: 0.2s;
  box-shadow: 0 8px 10px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.edit-button {
  cursor: pointer;
  font-weight: 600;
  font-size: 11px;
  height: 21px;
  justify-content: space-around;
  width: 20px;
  margin-left: 5px;
  transition-duration: 0.2s;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
.edit-icon {
  margin-inline-start: -2.5px;
}

.tasks-counter {
  display: flex;
  justify-content: center;
  margin-right: 10px;

  font-size: 0.7rem;
}

.checked {
  font-family: "Courier New", Courier, monospace;
  color: red;
  border: 5px inset red;
  background-color: #f4472d;
}

h1 {
  font-size: 18px;
  font-family: cursive;
  cursor: text;
}
